body {
  font-family: 'Raleway', sans-serif;
  display: flex;
  justify-content: center;
  min-height: 100vh;
  min-width: 100vw;
  margin: 0%;
}

.header {
  height: 27vh;
  background-color:rgba(60, 110, 113, 1) ;
  font-size: x-large;
  color: aliceblue;
  display: flex;
  flex-direction: column;
  justify-content: left;
  width: 100vw;
  z-index: 1;
}

p{
  margin: 1% 0% 0% 6%;
}
 
.horizontal-card {
  height: 35vh;
  width: 80vw;
  box-shadow: 2px 2px 2px rgb(217, 214, 214)
  ;
  display: flex;
  justify-content: center;
  background-color: rgb(246, 248, 248);
  margin-left: 10%;
  margin-right: 10%;
  justify-content: space-around;
  align-items: center;
  padding: 2%;
}

.vertical-card{
  height: auto;
  width: auto;
  box-shadow: 5px 5px 5px rgb(217, 214, 214)
  ;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: rgb(246, 248, 248);
  margin-left: 2%;
  margin-right: 2%;
}

.sections{
  display: flex;
  flex-direction: column;
  align-items: center;

}

img{
  height: 100px;
  width: 100px;
  margin: 3%;
}

h2 {
  text-align: center;
  font-size: xx-large;
  margin-top: 4%;
}

.next{
  display: flex;
  flex-direction: row;
  padding-left: 8%;
  padding-right: 8%;

}

.biopic{
  margin: 2%;
  border-radius: 50% 50%;
  height: 200px;
  width: 200px;
}

a:hover {
  color: #82846D;
  transition: 0.3s;
  transform: scale(1.03, 1.03);
}
a:link {
    color: black;
    text-decoration: none;
  }
  
  a:visited {
    color: black;
  }

.box-overlay{
  position: absolute;
  top: 10%;
  z-index: 2;
}

.webmargin{
  margin-top: 12%;
}

h3{
  font-size: xx-large;
  text-align: center;
  margin-top: 0%;
  margin-right: 25%;
}
.links{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
.lkimg{
  height: 50px;
  width: 150px;
  margin: 10%;
}

.skills{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  height: auto;
}

.logos{
  width: 100%;
  height: auto;
  margin: 0;
}

.demologo{
  width: 100%;
  height: 25vh ;
  margin: 0;
}
.footer{
  margin-top: 2%;
}
.resume{
  width: 450px;
  height: 460px;
  margin: 0;
}

.resume-card{
  height: auto;
  width: 450px;
  box-shadow: 5px 5px 5px rgb(217, 214, 214)
  ;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: rgb(246, 248, 248);
  margin-left: 2%;
  margin-right: 2%;
  align-items: center;
}

.make-it-centered{
  display: flex;
  align-items: center;
  justify-content: center;
}